import Vue from 'vue'

const appExplorer = {
  state: {
    account: {},
    topAppKey: '-1',
    appTab: {},
    // 网络状态
    netState: false,
    isDev: false
  },
  mutations: {
    // 设置是否是dev模式
    SET_ISDEV: (state, data) => {
      state.isDev = data
    },
    // 设置网络状态
    SET_NETSTATE: (state, data) => {
      state.netState = data
    },
    // 设置用户信息
    SET_ACCOUNT: (state, data) => {
      state.account = data
    },
    SET_APPTAB: (state, data) => {
      switch (data.flag) {
        case 0: {
          if (state.topAppKey === '-1') {
            Vue.set(state.appTab, data.val.appID, data.val)
          } else {
            state.appTab[state.topAppKey].isActive = false
            Vue.set(state.appTab, data.val.appID, data.val)
          }
          state.topAppKey = data.val.appID
          break
        }
        case 1: {
          if (data.val.isActive) {
            let appTabKeys = Object.keys(state.appTab)
            let i = appTabKeys.indexOf(data.val.appID.toString())
            i = i === 0 ? i + 1 : i - 1
            state.appTab[appTabKeys[i]].isActive = true
            state.topAppKey = appTabKeys[i]
            Vue.delete(state.appTab, data.val.appID)
          } else {
            Vue.delete(state.appTab, data.val.appID)
          }
          break
        }
        case 2: {
          state.appTab[state.topAppKey].isActive = false
          state.appTab[data.val.appID].isActive = true
          state.topAppKey = data.val.appID
        }
      }
    }
  }
}

export default appExplorer



