// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import store from './store'
import wui from './components/common/index'
import iconfont from './assets/iconfont/iconfont.css'
import i18n from './assets/i18n/i18n.js'

Vue.config.productionTip = false
//  加载SDK接口
let agent = navigator.userAgent.toLowerCase()
let ipc = null
let service = null
let openDebugModel =false
let isWinXpVersion = false
console.log('agent:::', window)

if (agent.indexOf('electron') !== -1) {
  ipc = window.require('electron').ipcRenderer
  service = window.require('electron').remote.getGlobal('service')
  openDebugModel = window.require('electron').remote.getGlobal('openDebugModel')
  i18n.locale = window.require('electron').remote.getGlobal('service').config.getLanguage();
  isWinXpVersion = window.require('electron').remote.getGlobal('isWinXpVersion')
} else {
  ipc = ''
  service = ''
}

Vue.prototype.$Service = service
Vue.prototype.$openDebugModel = openDebugModel
Vue.prototype.$ipc = ipc
Vue.use(iconfont)
Vue.use(wui)
Vue.prototype.$isWinXpVersion = isWinXpVersion

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  i18n,
  components: { App },
  template: '<App/>'
})



