<template>
  <div class="frame-error">
    <img :src="src">
    <span class="net-title">{{$t('failure')}}</span>
  </div>
</template>
<script>
export default {
  data() {
    return {
      src: require('./../../assets/null-page.png')
    }
  }
}
</script>

<style lang="scss">
.frame-error{
  width: 100%;
  height: 100%;
  img, .net-title{
    position: absolute;
    top: 190px;
    left: 50%;
    transform: translateX(-50%);
  }
  .net-title{
    font-size: 14px;
    top: 400px;
  }
}
</style>



