const getters = {
  app_tab: state => state.appExplorer.appTab,
  current_appID: state => state.appExplorer.topAppKey,
  user_account: state => state.appExplorer.account,
  net_state: state => state.appExplorer.netState,
  isDev: state => state.appExplorer.isDev
}
export default getters



