<template>
  <div class="web-content">
    <Error v-if="net_state&&!isloaded"></Error>
    <webview
      v-else
      src=""
      disablewebsecurity
      nodeintegration
      autosize
      minwidth="100%"
      minheight="100%"
      class="web-view"
      :id="id"
      preload="./static/js/vrv-jssdk.js"
    ></webview>
  </div>
</template>
<script>
import Error from './error.vue';
import { mapGetters } from 'vuex';
import { getErrorMsg} from '../common/util.js'
export default {
  components: {Error},
  data() {
    return {
      isloaded: false,
    }
  },
  computed: {
    ...mapGetters(['net_state', 'app_tab', 'isDev']),
  },
  props: {
    id: String,
    src: String,
    openOAuth:Boolean,
    accountType:Number
  },
  mounted(){
    if (Object.keys(this.app_tab).length === 0) return;
    const webview = document.getElementById(this.id);
    // webview.getWebContents().session.clearStorageData({origin:this.src,storages:['cookies']},(error)=>{
    //     console.log('error:',error);
    // })

    //electron版本高于1.1.3的可以在挂载的时候清理session数据
    if(!this.$isWinXpVersion){
      const webcontents = webview.getWebContents();
      console.log('webContents:',webcontents);
      const ses = webcontents.session;
      ses.clearStorageData({origin:this.src,storages:['cookies']},(error)=>{
          console.log('error:',error);
      })
    }

    if(this.openOAuth){
      this.$Service.auth.getClientKey(null,resp=>{
        webview.setUserAgent(`${webview.getUserAgent()} Linkdood pc_imlinkdood`);
        console.log('getClientKeyresp:',resp,'accountType:',this.accountType,'userAgent:',webview.getUserAgent());
        webview.loadURL(this.src,{extraHeaders:`imlinkdood-clientkey:${resp.clientkey}\nimlinkdood-accounttype:${this.accountType}`});
        this.addListener();
      });
    }
    else{
     webview.src=this.src;
     this.addListener();
    }

    console.log('openOAuth:',this.openOAuth);
  },
  created() {
    if (Object.keys(this.app_tab).length === 0) return;
    window.addEventListener('focus', () => document.getElementById(this.id).focus())
    //this.addEvent();
  },
  methods: {
    addListener(){
      const webview = document.getElementById(this.id);
      webview.addEventListener('did-stop-loading', this.loadstop);
            //'cookies','localstorage', 'caches', 'indexdb'
      // dom准备就绪事件，判断如果是dev模式下，则打开webview的openDevTools
      webview.addEventListener('dom-ready', () => {if (this.isDev||this.$openDebugModel) webview.openDevTools()});
      webview.addEventListener('ipc-message', this.openToast);
      webview.addEventListener('did-stop-loading', this.getFocus);
    },
    getFocus(){
      // 处理有部分应用输入框无法选中焦点的问题
      const webview = document.getElementById(this.id);
      webview.blur();
      webview.focus();
    },
    // 为当前webview添加加载完成事件
    addEvent() {
      // 获取当前webview
      const webview = document.getElementById(this.id);
      let thr; // 此变量用于清除定时器
      /**
       * 如果通过id获取到得webview为null,
       * 则过上一秒重新获取
       * 如果webview准备就绪，则清除定时器，给webview添加加载结束事件
       */
      if (webview === null) {
        thr = setTimeout(() => {
          console.log('webview null');
          this.addEvent();
        }, 1000)
        return;
      }
      clearTimeout(thr);
      console.log('webview:::', webview);
      webview.addEventListener('did-stop-loading', this.loadstop);
            //'cookies','localstorage', 'caches', 'indexdb'
      // webview.getWebContents().session.clearStorageData({origin:this.src,storages:['cookies']},(error)=>{
      //   console.log('error:',error);
      // })
      // dom准备就绪事件，判断如果是dev模式下，则打开webview的openDevTools
      webview.addEventListener('dom-ready', () => {if (this.isDev) webview.openDevTools()});
      webview.addEventListener('ipc-message', this.openToast);
    },
    openToast(event){
      console.log('event.channel:',event.channel);
      let toastObj = Object.assign({},event.channel);
      let msg = toastObj.msg||getErrorMsg(toastObj.code);
      this.$toast.open({
      duration: toastObj.duration||3000,
      message: msg,
      position: 'is-top',
      type: toastObj.type
    });
    },
    loadstop() {
      console.log('加载成功');
      if (this.isloaded) return;
      
      // electron 1.1.3版本在此处无法获取webontens对象，必须在load之后获取,
      if(this.$isWinXpVersion){
        //清理缓存session
        const webview = document.getElementById(this.id);
        const webcontents = webview.getWebContents();
        console.log('xp-webContents:',webcontents);
        webcontents.session.clearStorageData({origin:this.src,storages:['cookies']},(error)=>{
            console.log('error:',error);
        })
      }
      this.isloaded = true
    }
  }
}
</script>
<style lang="scss">
.web-content{
  width: 100%;
  height: 100%;
  border: none;
  .web-view{
    width: 100%;
    height: 100%;
    border: none;
  }
}
</style>



