<template>
  <div class="tab-host">
    <div
      class="host-iframe"
      v-for="(app, index) in openedApp"
      :key="index"
      v-show="app.isActive"
      v-if="app.appID !== '0'"
    >
      <web-content :id="app.appID" :src="app.mainUrl" :openOAuth="getOpenOAuth(app)" :accountType="getAccountType(app)"></web-content>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex';
import WebContent from './webcontent/index.vue';
export default {
  components: {WebContent},
  computed: {
    ...mapGetters(['app_tab']),
    openedApp() {
      return this.app_tab
    }
  },
  methods:{
    getOpenOAuth(app){
      if(app.hasOwnProperty('openOAuth')){
        return app.openOAuth.open;
      }
       return false;
    },
    getAccountType(app){
      if(app.hasOwnProperty('openOAuth')){
        return app.openOAuth.accountType;
      }
       return 1;
    }
  }
  
}
</script>
<style lang="scss" scoped>
.tab-host{
  width: 100%;
  height: calc(100% - 30px);
  .host-iframe{
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
}
</style>



