<template>
  <div class="window-navbar">
    <div class="left">
      <div class="refresh-btn" :title="$t('refresh')">
        <i class="iconfont icon-shuaxin4" @click="refresh"></i>
      </div>
      <div class="refresh-btn" :title="$t('printfPdf')">
        <i class="iconfont icon-pdf1" @click="printfToPdf"></i>
      </div>
      <div v-if="isApp" class="refresh-btn" title="复制链接地址">
        <i class="iconfont icon-lianjie2" @click="copyLink"></i>
      </div>
      <div v-if="isApp" class="refresh-btn" title="收藏">
        <i class="iconfont icon-shoucang" @click="shoucang"></i>
      </div>
      <div v-if="isApp" class="refresh-btn" title="转发">
        <i class="iconfont icon-gongxiang" @click="zhuanfa"></i>
      </div>
      <div v-if="isApp" class="refresh-btn" title="默认浏览器打开">
        <i class="iconfont icon-fangwen" @click="defaultOpen"></i>
      </div>
    </div>
    <div class="center">{{userName}}</div>
    <div class="right">
      <i class="iconfont icon-zuixiaohua" @click="hideWindow"></i>
      <i
        class="iconfont icon-size"
        :class="isMaxWindow?'icon-zuidahua':'icon-yuanjiaofangkuang'"
        @click="maxWindow"
      ></i>
      <i class="iconfont icon-ego-guanbi" @click="closeWindow"></i>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
const fs = window.require('fs');
const electronDialog = window.require('electron').remote.dialog;
console.log('fs1111:',fs);
export default {
  data () {
    return {
      isApp: false,
      isMaxWindow: false,
      addNoteParam: {
        isNet: false,                         // 是否使用网络接口
        lastChgTime: new Date().getTime().toString(), // 最后修改时间,毫秒  创建时间
        localPath: '',                        // 本地文件地址*
        type: 5,                             // 1. 文本，2，图片，3，音频，4，视频，5，URL
        netType: 1,                           // 1. 本地， 0. 网络
        content: {},                          // 内容 最长4096个字符	    收藏消息 文本、URL 直接存储内容   非文本类型，存储附件地址，网络地址和本地地址均可。。
      },
      localNote: {
        title: '',                            // <标题 最长128字符
        isReminder: 0,                        // <是否是提醒
        rmdTime: 0,                           // <提醒时间
        isArchive: 0                          // <是否归档
      },
      netNote: {
        msgProperties: '',	                  // <消息属性
        fileName: '',                         // <文件名
        exts: '',                             // <扩展字段
        loginInfo: ''
      }
    }
  },
  props: {
    userName: String
  },
  computed: {
    ...mapGetters(['app_tab', 'current_appID', 'user_account'])
  },
  created() {
    console.log('this.$ipc:::', this.$ipc);
    // 监听窗口大小变化
    this.$ipc.on('sub-win-state', this.onWindowSize)
  },
  methods: {
    /***
     * 双击双击导航栏更改窗口后，同事更改导航栏左边控制按钮的状态
     */
    onWindowSize(e, state) {
      if (state === 'maximize') this.isMaxWindow = true;
      if (state === 'unmaximize') this.isMaxWindow = false;
    },

    // 隐藏窗口
    hideWindow () {
      console.log();
      this.$ipc.send('app-window-control', 'minimize');
    },

    // 最大化窗口
    maxWindow () {
      this.isMaxWindow = !this.isMaxWindow;
      let winSize = this.isMaxWindow? 'maximize' : 'unmaximize'
      this.$ipc.send('app-window-control', winSize);
    },

    // 关闭窗口
    closeWindow () {
      this.$ipc.send('app-window-control', 'close');
    },

    // 刷新
    refresh() {
      document.getElementById(this.current_appID).reload();
    },
    printfResult(result){
      this.$toast.open({
          duration: 3000,
          message: result? this.$t('printfPdfSucc'):this.$t('printfPdfFail'),
          position: 'is-top',
          type: result? 'is-success' : 'is-error'
      });
    },
    printfToPdf(){
      const options = {
        title: this.$t('printfPdf'),
        filters: [
          { name: 'Images', extensions: ['pdf'] }
        ]
      }
      electronDialog.showSaveDialog(options, (filename)=>{
        console.log('fileName:',filename)
        if(filename){
          let webContent= document.getElementById(this.current_appID).getWebContents();
          webContent.printToPDF({}, (error, data) => {
            if (error){
              this.printfResult(false);
              throw error;
            }
            fs.writeFile(filename, data, (error) => {
              if (error){
                this.printfResult(false);
                throw error
              }
              this.printfResult(true);
              console.log('Write PDF successfully.')
            })
          })

        }
      })
    },

    // 复制地址
    copyLink() {
      if (this.app_tab[this.current_appID].isLocalApp) {
        return
      }
      let textarea = document.createElement('textarea');
      textarea.value = this.app_tab[this.current_appID].mainUrl;
      console.log(textarea);
      document.body.appendChild(textarea);
      textarea.select();
      document.execCommand('copy')
      document.body.removeChild(textarea)
    },

    // 收藏
    shoucang() {
      if (this.app_tab[this.current_appID].isLocalApp) {
        return
      }
      // 公用参数
      this.addNoteParam.sendUserId = this.user_account.id,     //发送的人 消息来源Id
      this.addNoteParam.targetId = this.user_account.id,       //来源 人或者群  群Id
      this.addNoteParam.content.uploadUser = this.user_account.name;
      this.addNoteParam.content.body = this.app_tab[this.current_appID].mainUrl;
      // 本地收藏参数
      this.localNote.key = this.user_account.name,
      this.addNoteParam.content = JSON.stringify(this.addNoteParam.content);
      Object.assign(this.addNoteParam, this.addNoteParam.netType ? this.localNote : this.netNote);
      console.log(this.addNoteParam, '添加收藏入参Obj');
      this.$Service.enterprise.addNote(
        this.addNoteParam,
        resp => {
          console.log(resp, 'add note cb')
          if (resp.code === 0) {
            // ..
          }
        }
      )
    },

    // 转发
    zhuanfa() {
      // ...
    },

    // 默认浏览器打开
    defaultOpen() {
      if (this.app_tab[this.current_appID].isLocalApp) {
        return
      }
      this.$ipc.send('new-window-weburl', this.app_tab[this.current_appID].mainUrl)
    }
  }
}
</script>

<style lang="scss">
.window-navbar{
  width: 100%;
  height: 40px;
  background: #cbcbcb;
  display: flex;
  justify-content: space-between;
  -webkit-app-region: drag;
  & > div{
    height: 100%;
    -webkit-app-region: no-drag;
  }
  .left{
    display: flex;
    justify-content: flex-start;
    .refresh-btn{
      width: 40px;
      height: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      .iconfont{
        font-size: 18px;
        &:hover{
          color: #0065ff;
          cursor: pointer;
        }
      }
      .icon-shoucang{
        font-size: 20px;
      }
      .icon-gongxiang{
        font-size: 22px;
      }
    }
  }
  .center{
    display: flex;
    align-items: center;
    font-weight: 600;
    // background: rgb(0, 255, 42);
  }
  .right{
    width: 90px;
    padding-right: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .iconfont{
      font-size: 15px;
      cursor: pointer;
      &:hover{
        color: #0065ff;
      }
    }
    .icon-size{
      font-size: 12px;
    }
  }
}
</style>



