<template>
  <div class="app-tab">
    <div
      class="tab-item"
      v-for="(item, index) in app_tab"
      :key="index"
      :title="item.appName"
      :style="{background: item.isActive ? '#fff' : ''}"
    >
      <div
        class="item-content"
        :class="{'is-select-item': item.isActive}"
      >
        <img class="app-icon" :src="item.appIcon" @error="imgError">
        <span @click="selectItem(item, index)">{{item.appName}}</span>
        <i
          class="iconfont icon-guanbi1"
          :title="$t('close')"
          v-if="index != 0"
          @click="closeItem(item, index)"
        ></i>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'AppExplorer',
  data () {
    return {
      defaultIcon: require('./../assets/app.png')
    }
  },
  computed: {
    ...mapGetters(['app_tab'])
  },
  methods: {
    imgError(e) {
      e.target.src = this.defaultIcon;
    },
    selectItem (item, index) {
      console.log(item, index);
      if (item.isActive) return;
      let addData = {
        appID: item.appID
      };
      this.$store.commit('SET_APPTAB', {flag: 2, val: addData})
    },
    closeItem (item, index) {
      if (Object.keys(this.app_tab).length === 1) {
        this.$ipc.send('app-window-control', 'close');
        return
      };
      this.$store.commit('SET_APPTAB', {flag: 1, val: item})
    }
  }
}
</script>

<style lang="scss" scoped>
.app-tab{
  width: 100%;
  height: 30px;
  background: #e4e4e4;
  display: flex;
  justify-content: flex-start;
  .tab-item{
    cursor: default;
    width: 170px;
    height: 100%;
    font-size: 14px;
    border-radius: 8px 8px 0 0;
    overflow: hidden;
    position: relative;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    text-overflow: ellipsis;
    &:hover{
      background: #f1f1f1;
    }
    .item-content{
      width: 100%;
      height: 20px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      overflow: hidden;
      padding: 0 5px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      .app-icon{
        width: 15px;
        height: 15px;
        border-radius: 4px;
      }
      span{
        display: inline-block;
        width: 88%;
        overflow: hidden;
        text-overflow: ellipsis;
        padding-left: 5px;
      }
      .icon-guanbi1{
        font-size: 10px;
        cursor: pointer;
        display: inline-block;
      }
    }
    .is-select-item{
      // border-right: 1px solid #0000004a;
      // border-left: 1px solid #0000004a;
    }
  }
}
</style>



