import i18n from '../../assets/i18n/i18n.js';
export function editBoxGetFocus (el) {
  el.focus()
  var range = document.createRange()
  range.selectNodeContents(el)
  range.collapse(false)
  var sel = window.getSelection()
  sel.removeAllRanges()
  sel.addRange(range)
}

export function encryptionPassword () {
  let num = ''
  for (let i = 0; i < 16; i++) {
    num += Math.floor(Math.random() * 10)
  }
  return num
};
// 统一入口处理错误码提示
export function getErrorMsg(code) {
  let ErrorCode = i18n.messages[`${i18n.locale}`].ErrorCode;
  let errorMsg = ErrorCode[code] ? ErrorCode[code] : `${i18n.t('unknown') + code}`
  return errorMsg
}



