<template>
  <div id="app">
    <WindowNavbar :userName="userName"></WindowNavbar>
    <AppExplorer :preLoginAddress="preLoginAddress"></AppExplorer>
  </div>
</template>

<script>
import AppExplorer from './components/index.vue';
import WindowNavbar from './components/window-navbar.vue';
export default {
  name: 'App',
  data () {
    return {
      preLoginAddress: '',
      userName: ''
    }
  },
  components: {
    WindowNavbar,
    AppExplorer
  },
  created () {
    if (!navigator.onLine) {
      console.log(navigator.onLine);
      this.$store.commit('SET_NETSTATE', true)
    }
    let agent = navigator.userAgent.toLowerCase();
    if (agent.indexOf('electron') !== -1) {
      // 监听主进程传递的appData
      this.$ipc.on('open-appData', (event, appData)=>{
        console.log(event, appData);
        this.$set(appData, 'isActive', true);
        this.$store.commit('SET_APPTAB', {flag: 0, val: appData})
      });
      // 获取账号信息
      this.$Service.user.getAccountInfo(
        null,
        resp => {
          console.log(resp, '获取账户信息回调');
          this.$store.commit('SET_ACCOUNT', resp);
        }
      );
      // 监听网络状态信号
      this.$ipc.on('net-status', (e, state) => {
        console.log('网络状态:::', state);
        this.$store.commit('SET_NETSTATE', state)
      })
      // 监听是否为dev模式启动
      this.$ipc.on('is-dev', (e, state) => {
        console.log('以dev模式打开:::', state);
        this.$store.commit('SET_ISDEV', state)
      })
    } else {
      this.$store.commit('SET_ISDEV', false)
      let testData = {
        appName: '工作台',
        appIcon: require('./assets/work.png'),
        isActive: true,
        appID: '1111',
        isLocalApp: true,
        mainUrl: 'https://baidu.com'
      };
      this.$store.commit('SET_APPTAB', {flag: 0, val: testData})
    }
  },
  mounted() {
    document.addEventListener('dragenter', function(event) {
      event.preventDefault();
    }, false);
    document.addEventListener('dragover', function(event) {
      event.preventDefault();
    }, false);
    document.addEventListener('drop', function(event) {
      event.preventDefault();
    }, false);
    document.addEventListener('dragend', function(event) {
      event.preventDefault();
    }, false)
  }
}
</script>

<style lang="scss">
#app {
  height: 100%;
  font-family: '微软雅黑', 'Microsoft YaHei', Arial, Helvetica, sans-serif, '宋体';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
</style>



