<template>
  <div class="app-explorer">
    <div
      class="net-state"
      v-if="net_state"
    >
      <i class="iconfont icon-gantanhao_icon"></i>
      {{$t('netState')}}
    </div>
    <AppTab></AppTab>
    <Content
      :preLoginAddress="preLoginAddress"
    ></Content>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import AppTab from './app-tab.vue';
import Content from './content'
export default {
  data () {
    return {
      appInfo: {}
    }
  },
  props: {
    preLoginAddress: String
  },
  computed: {
    ...mapGetters(['net_state'])
  },
  components: {
    AppTab,
    Content
  }
}
</script>

<style lang="scss" scoped>
.app-explorer{
  width: 100%;
  height: calc(100% - 40px);
  .net-state{
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px 20px;
    background: #ff5252;
    color: #fff;
    margin: 0 auto;
    z-index: 999;
    i{
      margin-right: 10px;
    }
  }
}
</style>



